import React, { useEffect } from 'react';
import './ProductCard.css';
import { Link } from 'react-router-dom';
import { motion, useAnimation } from "framer-motion";
import { useInView } from "react-intersection-observer";
import BtnLink from './BtnLink';

const ProductCard = (props) => {

  const boxVariant = {
    visible: { opacity: 1, scale: 1, translateX: 0, transition: { duration: 0.4 } },
    hidden: { opacity: 0.25, scale: 0.65, translateX: 180 },
  }

  const control = useAnimation()
  const [ref, inView] = useInView()

  useEffect(() => {
    if (inView) {
      control.start("visible");
    }
    else {
      control.start("hidden");
    }
  }, [control, inView]);


  return (
    <motion.div
    ref={ref}
    variants={boxVariant}
    initial="hidden"
    animate={control}
    className='product-card'>
    <div className='product-img'><img className='w-100' loading='lazy' src={process.env.REACT_APP_BASE_URL + "images/icons/" + props.imgSrc} alt={props.productName} title={props.productName} /></div>
    <div className='product-name-box'>
      <span className='product-name-title' >By Bagophile</span>
      <Link className='product-name' to={props.pdLink}>{props.productName}</Link>
    </div>
  </motion.div>
  )
}

export default ProductCard
